$c1-main: #000000;
$c2-main: #ffffff;
$c3-main: #424242;
$c4-main: #707070;

$c1-second: #50b375;
$c2-second: #2e6c48;
$c3-second: #EFFCEF;
$c4-second: #FF5454;

$c-white: #fefefe;
$c-black: #010101;
$c-grey: #999999;
$c-darkGrey: #2F2F2F;
$c-lightGrey: #959595;
$c-lightGreyInput :#EDEDED; 

$c-success: #5cb85c;
$c-warn: #f0ad4e;
$c-error: #d9534f;

$vertical-gradient: linear-gradient(360deg, rgba(46,108,72,1) 35%, rgba(80,179,117,1) 100%);
$horizontal-gradient: linear-gradient(270deg, rgba(46,108,72,1) 0%, rgba(80,179,117,1) 100%);
$transition: 0.3s;
$box-shadow: #01010159;

/* --------------------------------------------------

Fonts

-------------------------------------------------- */

$font-main-1: "Maven Pro Regular";
$font-main-2: "Maven Pro Bold";
$font-main-3: "Maven Pro Black";


/* --------------------------------------------------

Breakpoint Settings

-------------------------------------------------- */

$media-direction: min; // min / max  default is min (mobile first)

$breakpoints: (
  "xs": 0,
  "sm": 576px,
  "md": 768px,
  "lg": 992px,
  "xl": 1200px,
  "xxl": 1920px
);
