.header {
  &:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    opacity: 0.2;
    background-color: $c-black;
  }

  display: block;
  margin: 0 auto 6rem auto;

  &__image {
    width: 100%;
    object-fit: cover;
    object-position: 40% 100%;
    height: 100vh;

    @include breakpoint("xl") {
    }
  }

  &__wrapper {
    position: absolute;
    margin: 35px;
    top: 3rem;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;

    @include breakpoint("sm") {
      margin: 45px;
    }
    
    @include breakpoint("lg") {
      top: 4rem;
      margin: 55px;
      margin-right: 80px;
    }
    
    @include breakpoint("xl") {
      right: 10rem;
      margin: 80px;
      margin-right: 10rem;
    }

    @include breakpoint("xxl") {
      margin-right: 15rem;
    }
  }

  &__title {
    color: $c-white;
  }

  &__p {
    margin-bottom: 1.5rem;
    color: $c-white;

    @include breakpoint("sm") {
      margin-bottom: 3.5rem;
    }

    @include breakpoint("lg") {
      margin-bottom: 5rem;
    }

    @include breakpoint("xl") {
      margin-bottom: 7.5rem;
    }

    @include breakpoint("xxl") {
      margin-bottom: 10rem;
    }
  }

  &__btn {
    &__wrapper {
      display: flex;
      flex-direction: column;

      @include breakpoint("sm") {
        margin: 0 2rem;
        flex-direction: row;
        margin: 0;
      }
    }
  }

  &__secondBtn {
    margin-top: 1rem;

    @include breakpoint("sm") {
      margin-top: 0rem;
      margin-left: 2rem;
    }
  }
}
